@import url("https://fonts.googleapis.com/css?family=Nunito+Sans:300,600,800&display=swap");
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
  transition: background-color 300ms linear; }

body {
  margin: 0; }

.container {
  padding: 1.75rem 2rem; }
  @media screen and (min-width: 768px) {
    .container {
      padding: 1.75rem 4rem; } }
  @media screen and (min-width: 1395px) {
    .container {
      padding-left: 8rem;
      padding-right: 8rem; } }

.fa-ellipsis-h {
  color: #858585; }

.dark {
  background: #202c37;
  color: white; }
  .dark * {
    color: white; }

.light {
  background: #fafafa !important;
  color: #111517 !important; }
  .light input {
    color: #858585 !important; }

.darkElements {
  background: #2b3945; }
  .darkElements * {
    color: white; }
  .darkElements input::placeholder {
    color: white; }

.lightElements {
  background: white !important; }

/* styles for the root element
we set the root element as a flex item
with this kind of display
we can set a specific element take the remaining height of
the screen
thanks to flex-grow ;) */
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.loadingGif {
  width: 64px !important;
  height: 64px !important;
  margin: auto; }

.pagination {
  margin-top: 30px; }
  .pagination * {
    font-size: 1.1rem;
    font-weight: 800;
    user-select: none; }
  .pagination button {
    background: none;
    border: none;
    cursor: pointer;
    outline: none; }
    .pagination button:disabled {
      cursor: default; }
      .pagination button:disabled i {
        color: #858585 !important; }
  .pagination .nextPage i {
    transform: rotate(180deg); }
  .pagination .isActive {
    color: #00a6cf; }
  .pagination .elipsis {
    cursor: default; }
    .pagination .elipsis i {
      color: #858585; }
